.backButton {
  width: 20px;
  cursor: pointer;
}

.player-details-title b {
  padding: 10px;
}

h2 {
  text-align: center;
  font-family: "Forgotten Futurist";
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 1rem;
  letter-spacing: 0.05em;
}

.game-details-header {
  margin: 0.5rem 1.5rem;
}

.game-details-header div,
.add-player {
  font-family: "Forgotten Futurist";
  margin: 0.5rem 0;
  letter-spacing: 2px;
  color: #2084fa;
  font-size: 1.25rem;
  font-weight: 700;
}

.add-player {
  font-size: 1rem;
  font-weight: 400;
  cursor: pointer;
}

.game-details-input {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 20px;
  margin-left: 1rem;
}

.players .game-details-input {
  margin: initial;
}

.game-player-details {
  margin-top: 2rem;
}

.player-details-title {
  font-family: "Gilroy";
  font-weight: 600;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
  letter-spacing: 1px;
}

.arma-container .cell>span {
  font-weight: 700;
}

.arma-container input,
.arma-container select {
  border-radius: 10px;
  border: 1px solid black;
  padding: 0.75rem;
  color: black;
  width: 275px;
}

.arma-container select{
  width: 275px;
}

.extra-menu {
  background: #f4f4f4;
  border-radius: 15px;
  padding: 2rem;
  width: 80%;
  position: relative;
  margin: 2rem auto 0;
  height: 84%;
}

.extra-menu img {
  height: 100%;
}

.extra-menu .games div {
  font-family: "Gilroy";
  font-weight: 600;
  margin: 1.5rem 0;
  cursor: pointer;
  font-size: 1.25rem;
  letter-spacing: 0.05em;
}

.game-tabs {
  border-bottom: 1px solid #000000;
  padding-bottom: 0.5rem;
  width: 70%;
}

.game-tabs span {
  padding: 0 1rem 6px 1rem;
  cursor: pointer;
  font-family: 'Montserrat';
}

.game-tab-active {
  border-bottom: 3px solid #000000;
}

.all-games,
.bits-games {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.players {

  border-radius: 15px;
  padding: 0 1rem 1rem;
  margin-top: 1rem;
}

.save-players {
  font-family: "Gilroy";
  padding: 0.5rem 2rem;
  background-color: #000000;
  outline: 1px solid #000000;
  outline-offset: 3px;
  color: #ffffff;
  font-size: 1rem;
  border: 0;
  font-weight: 700;
  position: relative;
  cursor: pointer;
  margin: 1rem 1.5rem;
}

.arma-back {
  background: white;
  color: black;
}

.arma-cta-container {
  display: flex;
  justify-content: center;
}

.arma-container .MuiFormLabel-root.Mui-focused {
  color: black !important;
  border-color: black !important;
}

.arma-container .MuiOutlinedInput-notchedOutline {
  border: none;
  border-radius: 10px !important;
}

.arma-container .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border: 1px solid black;
  border-radius: 10px;
}

.arma-container .MuiOutlinedInput-input {
  border: 1px solid black;
  border-radius: 10px;
  color: black !important;
}

.arma-container .MuiInputBase-input-MuiOutlinedInput-input {
  border: 1px solid black;
}

.arma-container .MuiSvgIcon-root {
  color: black !important;
}

.players::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

.players::-webkit-scrollbar-thumb {
  width: 3px;
  background-color: black;
  border-radius: 3px;
}

.arma-fluid {
  margin-top: 2.5%;
}

.nav-arma {
  position: relative;
  right: 50px;
}

@media screen and (max-width: 768px) {
  .game-details-input {
    display: flex;
    flex-direction: column;
    margin-left: 0;
  }

  .players {
    padding: 0;
  }

  .extra-menu {
    width: 100%;
    padding: 1rem;
  }

  .extra-menu .games div {
    margin: 1rem 0;
  }

  .extra-menu div:first-child {
    width: 100%;
  }

  .game-tabs span {
    padding: 0.5rem 0.5rem 6px;
  }

  .extra-menu .img {
    display: none;
  }

  .all-games,
  .bits-games {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }

  .arma-container input,
  .arma-container select {
    width: 65%;
  }
  .arma-wrapper .logo{
    display: none;
  }
}